@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-nueva-tarea{
    display: flex;
    justify-content: center;
    margin-top: 5rem;

    .agregar-tarea{
        @include m.boton(v.$cyan, v.$blanco);
    }

}

.listado-tareas{
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    .no-tareas{
        text-align: center;

    }

}




    .tarea{
        padding: .5rem 2rem;
        border-bottom: 1px solid darken(v.$grisClaro, 10%);
        margin-bottom: 1rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        &:last-of-type{
            border: none;
        }

        p{
            font-size: 1.8rem;
        }

        .opciones{
            display: flex;
            gap: 2rem;

            button{
                border: none;
                font-size: 1.5rem;
                padding: .5rem 1rem;
                font-weight: 700;
                border-radius: .5rem;
                cursor: pointer;
            }

            .completa{
                background-color: v.$azul;
                color: v.$blanco;
            }

            .pendiente{
                background-color: v.$naranja;
                color: v.$blanco;
            }

            
            .eliminar-tarea{
                background-color: v.$rojo;
                color: v.$blanco;
            }
        }
        
    }
    


