@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard{
    background-color: v.$grisClaro;

    @include m.tablet{
        display: flex;
        height: 100vh;
    }

    .principal{
        flex: 1;
        @include m.tablet{
            overflow: scroll; //muy bueno para solo scroolear un lado, si sobrepasa de la pantalla     
        }

    }

    .contenido{
        padding: 2rem;
        .nombre-pagina{
            font-size: 3rem;
            text-align: center;
          
        }
    }

    .formulario{
        input:not([type="submit"]){
        background-color: v.$blanco;
    }

    input[type="submit"]{
        @include m.boton(v.$indigo, v.$blanco);
        padding: 1rem 2rem;
       
    }

    }
    .enlace{
        color: v.$indigo;
        font-weight: bold;
        &:hover{
            color: darken(v.$indigo, 20%);
        }
    }
}