@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra-mobile{
    background-color: v.$indigo;
    padding: 2rem 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include m.tablet{
        display: none;
    }
    h1{
        color: v.$blanco;
        margin: 0;
    }

    .menu{
        img{
            width: 5rem;
            cursor: pointer;
        }
    }

}

.barra{
    display: none;

    @include m.tablet{
        background-color: v.$blanco;
        padding: 1rem 2rem;
        box-shadow: 1px 12px 9px -13px lighten(v.$negro, 30%);
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    p{
        margin: 0;
        color: v.$negro;
        span{
            font-weight: 700;
            color: v.$gris;
        }
    }
    .cerrar-sesion{
        @include m.boton(darken(v.$indigo, 20%), v.$blanco);
        border-radius: .5rem;
        font-size: 1.5rem;
        padding: 1rem;
    }

}